  takePhotograph(op) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
       if(op==1){     this.f1 	= image.toString();}
       if(op==2) {    this.f2 	= image.toString();}

     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }


  selectImage(op) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
      if(op==1){     this.f1 	= image.toString()};
      if(op==2)  {   this.f2 	= image.toString()};


     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }




  public processWebImage(event,op): void {



    if(event.target.files && event.target.files[0]){

      this.videomira=true
      const loading = this.loadingCtrl.create({
        spinner: 'bubbles',
        content: "Espere un momento....."
      });
      loading.present();

          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {

          if(this.opimg==1)
          {
            this.filegenerado1=event
            this.f1 = readerEvent.target.result;
            this._IMAGE.generateFromImage(this.f1, 300, 300, 2, data => {
            this.f1 = data;

            loading.dismiss()
                if(this.automatico==true)
                {
                  this.select1.open();

                }


          });
          }

          if(this.opimg==3)
          {
            this.filegenerado=event
            this.f3 = readerEvent.target.result;


             let encodingType = "data:video/mp4;base64,";

             let OriginalBase64 = readerEvent.target.result.split(',')[1]; // Remove the "data:video..." string.
             let decodedBase64 = atob(OriginalBase64); // Decode the incorrectly encoded base64 string.
             let encodedBase64 = btoa(decodedBase64); // re-encode the base64 string (correctly).
             let newBase64 = encodingType + encodedBase64; // Add the encodingType to the string.

             if((readerEvent.loader/1024)<=10)
             {

              this.f3=''
              let video = this.myVideo.nativeElement;
              video.src = '' ;
              this.videomira=false
              this.msj.showError3('Error','El tamaño de los videos debe ser < 10 MEGAS');

             }
             else{

              let video = this.myVideo.nativeElement;
              video.src = newBase64 ;
              this.videomira=false

             }

             loading.dismiss()


           //  video.play();



          }

          if(this.opimg==2)
          {
            this.filegenerado2=event

            this.f2 = readerEvent.target.result;
            this._IMAGE.generateFromImage(this.f2, 300, 300, 2, data => {
            this.f2 = data;
            loading.dismiss()




          });
          }

   };
      reader.readAsDataURL(event.target.files[0]);
    }
}



